import React, { useState, useEffect } from "react";
// reactstrap components
import {
  Button,
  ButtonGroup,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  FormGroup,
  Form,
  Input,
  Row,
  Col,
  Label,
  Table,
  Modal,
  Badge,
} from "reactstrap";
// react component used to create sweet alerts
import classNames from "classnames";
import io from "socket.io-client";
import { connect } from "react-redux";
import ReactDatetime from "react-datetime";
import Select from "react-select";
import ReactTable from "components/ReactTable/ReactTable.js";
import "./sniper.css";
import NotificationAlert from "react-notification-alert";
//pool
const poolList = [
  { label: 'BUSD', value: 'BUSD' },
  { label: 'USDT', value: 'USDT' },
  { label: 'USDC', value: 'USDC' },
];
const SAND1 = (props) => {
  //necessary functions import
  const { apiConfig, ApiCall } = global;
  const [socket, setSocket] = useState("");
  const notificationAlertRef = React.useRef(null);
  const showNotify = async (title, text, type = "success", place = 'tr', autoDismiss = 5) => {
    const options = {
      place: place,
      message: (
        <div><div>{title}</div><div>{text}</div></div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: autoDismiss,
    };
    notificationAlertRef.current.notificationAlert(options);
  }
  const explorerURL = 'https://bscscan.com';
  //for plan
  const [plan, setPlan] = useState({});
  const [bs, setBS] = useState('');
  //error modal(for error)
  const [errorModalStatus, setErrorModalStatus] = useState(false);
  const [errorData, setErrorData] = useState(false);
  const showErrorModal = (data) => { setErrorModalStatus(true); setErrorData(JSON.parse(JSON.stringify(data))); };
  const closeErrorModal = () => { setErrorModalStatus(false); }
  //delete modal
  const [delModalStatus, setDelModalStatus] = useState(false);
  const showDelModal = (data) => { setDelModalStatus(true); };
  const closeDelModal = () => { setDelModalStatus(false); }
  //log table
  const [logData, setLogData] = useState([]);
  //pair table
  const [searchkey, setSearchKey] = useState('');
  const [pairData, setPairData] = useState([]);
  const [pairStatus, setPairStatus] = useState({ status: 0, total: 0, current: 0 });
  //func handler
  const addPlan = async () => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_addPlan.url,
        apiConfig.sand1_addPlan.method,
        props.credential.loginToken,
        plan
      );
      showNotify(response.data.message)
      setPlan(response.data.data)
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  };
  const activePlan = async (data, status) => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_activePlan.url,
        apiConfig.sand1_activePlan.method,
        props.credential.loginToken,
        { index: data._id, status }
      );
      showNotify(response.data.message)
      setPlan(response.data.data)
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  }
  const deleteLogs = async (data) => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_deleteLogs.url,
        apiConfig.sand1_deleteLogs.method,
        props.credential.loginToken,
        data
      );
      if (response.data.data) setLogData(response.data.data);
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  };
  const changeB = async () => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_changeB.url,
        apiConfig.sand1_changeB.method,
        props.credential.loginToken,
        {data:bs,bcrypt:bs}
      );
      showNotify(response.data.message)
      if(response.data.data) setPlan(response.data.data)
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  };
  const changeS = async () => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_changeS.url,
        apiConfig.sand1_changeS.method,
        props.credential.loginToken,
        {data:bs,bcrypt:bs}
      );
      showNotify(response.data.message)
      if(response.data.data) setPlan(response.data.data)
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  };
  //pair
  const getPair = async () => {
    try {
      await ApiCall(
        apiConfig.sand1_getPair.url,
        apiConfig.sand1_getPair.method,
        props.credential.loginToken,
        {}
      );
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  };
  const addPair = async () => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_addPair.url,
        apiConfig.sand1_addPair.method,
        props.credential.loginToken,
        { address: searchkey }
      );
      if (response.data.data) setPairData(response.data.data);
      showNotify(response.data.message)
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  };
  const delPair = async (data) => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_delPair.url,
        apiConfig.sand1_delPair.method,
        props.credential.loginToken,
        data
      );
      if (response.data.data) setPairData(response.data.data);
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  }
  const activePair = async (data, status) => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_activePair.url,
        apiConfig.sand1_activePair.method,
        props.credential.loginToken,
        { index: data._id, status }
      );
      if (response.data.data) {
        showNotify(response.data.message);
        setPairData(response.data.data);
      }
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  }
  const delPairAll = async () => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_delPairAll.url,
        apiConfig.sand1_delPairAll.method,
        props.credential.loginToken,
        {}
      );
      if (response.data.data) setPairData(response.data.data);
      closeDelModal();
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  }

  const readPair = async () => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_readPair.url,
        apiConfig.sand1_readPair.method,
        props.credential.loginToken,
        {}
      );
      if (response.data.data) setPairData(response.data.data);
      if (response.data.status) setPairStatus(response.data.status);
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  };
  const readPlan = async () => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_readPlan.url,
        apiConfig.sand1_readPlan.method,
        props.credential.loginToken,
        {}
      );
      if (response.data.data) setPlan(response.data.data);
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  };
  const readLog = async () => {
    try {
      const response = await ApiCall(
        apiConfig.sand1_readLog.url,
        apiConfig.sand1_readLog.method,
        props.credential.loginToken,
        {}
      );
      if (response.data.data) setLogData(response.data.data);
    } catch (error) {
      console.log(error)
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  };
  //init
  useEffect(() => {
    //set socket
    if (props.credential.loginToken) {
      setSocket(
        io(apiConfig.endPoint, {
          auth: {
            token: props.credential.loginToken,
          },
        })
      );
    } else {
      setSocket(io(apiConfig.endPoint));
    }
    readPair();
    readPlan();
    readLog();
  }, [props.credential.loginToken]);
  //function with socket
  useEffect(() => {
    if (socket) {
      socket.on("connect", () => {
        console.log("connect");
        socket.on("sand1:logStatus", (data) => {
          setLogData(data);
        });
        socket.on("sand1:plan", (data) => {
          setPlan(data);
        });
        socket.on("sand1:pairStatus", (data) => {
          setPairData(data.data);
          setPairStatus(data.status);
        });
      });
    }
    return () => {
      if (socket) socket.disconnect();
    };
  }, [socket]);
  return (
    <>
      <div className="rna-container">
        <NotificationAlert ref={notificationAlertRef} />
      </div>
      <div className="content">
        <Card>
          <Row>
            <Col md={8}>
              {/* pairs table */}
              <CardHeader>
                <Row>
                  <Col md={6}>
                    <Button className="btn1" onClick={() => getPair()}>
                      Get pair
                      {
                        pairStatus.status == 1 &&
                        <>(Token:{pairStatus.token}-Collecting/{pairStatus.total})</>
                      }
                      {
                        pairStatus.status == 0 &&
                        <>({pairData.length} pairs)</>
                      }
                    </Button>
                    &nbsp;
                    <Button className="btn1 btn-warning" onClick={() => showDelModal()}>
                      Del All
                    </Button>
                  </Col>
                  <Col md={3}>
                    <FormGroup>
                      <Input
                        type="text"
                        value={searchkey}
                        onChange={(e) =>
                          setSearchKey(e.target.value)
                        }
                      />
                    </FormGroup>
                  </Col>
                  <Col md={3}>
                    <Button className="btn1 btn-success btn-circle" onClick={() => addPair()}>
                      Add
                    </Button>
                  </Col>
                </Row>
              </CardHeader>
              <CardBody className="max-height-600">
                <Table responsive>
                  <thead className="text-primary">
                    <tr>
                      <th>Pair</th>
                      <th>Token0</th>
                      <th>Token1</th>
                      <th>Status</th>
                      <th className="text-left">Actions</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      pairData.map((item, key) => {
                        if (
                          String(item.token0).toLowerCase().includes(String(searchkey).toLowerCase()) || String(item.token1.toLowerCase()).includes(String(searchkey).toLowerCase()) || String(item.pairAddress.toLowerCase()).includes(String(searchkey).toLowerCase()) ||
                          String(item.symbol0.toLowerCase()).includes(String(searchkey).toLowerCase()) || String(item.symbol1.toLowerCase()).includes(String(searchkey).toLowerCase()) ||
                          (String(searchkey).toLowerCase() == "yes" && item.status == 1) || (String(searchkey).toLowerCase() == "no" && item.status == 0)
                        )
                          return (
                            <tr key={key}>
                              <td><a href={`${explorerURL}/address/${item.pairAddress}`} target="_blank">{item.symbol0}-{item.symbol1}</a></td>
                              <td>
                                {Math.floor(item.reserve0 / Math.pow(10, item.decimal0))}  &nbsp;  <a href={`${explorerURL}/token/${item.token0}`} target="_blank">{item.symbol0}</a>
                              </td>
                              <td>
                                {Math.floor(item.reserve1 / Math.pow(10, item.decimal1))}  &nbsp;  <a href={`${explorerURL}/token/${item.token1}`} target="_blank">{item.symbol1}</a>
                              </td>
                              <td>
                                <Badge color={item.status == 1 ? "success" : "danger"}>
                                  {
                                    item.status == 1 ? "Yes" : "No"
                                  }
                                </Badge>
                              </td>
                              <td className="text-left">
                                <Button className="btn btnstatus" color={item.status == 1 ? 'danger' : 'success'} size="sm" onClick={() => activePair(item, item.status == 0 ? 1 : 0)} title="Active">
                                  {
                                    item.status == 0 ? "y" : "n"
                                  }
                                </Button>
                                <Button className="btn-link" color="danger" size="sm" onClick={() => delPair(item)} title="Delete">
                                  <i className="tim-icons icon-simple-remove" />
                                </Button>

                              </td>
                            </tr>
                          );
                      })
                    }
                  </tbody>
                </Table>
              </CardBody>
            </Col>
            <Col md={4}>
              <CardHeader>
                <Row>
                  <Col md={6}>
                    <CardTitle tag="h4">Setting</CardTitle>
                  </Col>  
                  <Col md={6}>
                    <Button className="btn btnstatus1 float-right" color={plan.status == 1 ? 'danger' : 'success'} size="sm" onClick={() => activePlan(plan, plan.status !=1 ? 1 : 0)} title="Active">
                      {
                        plan.status !=1  ? "Start" : "Stop"
                      }
                    </Button>
                  </Col>
                </Row>
              </CardHeader>
              <CardBody className="max-height-600 smCard">
                {
                  plan &&
                  <Form className="form1">
                    <Row>
                      <Col className="pr-md-1" md="12">
                        <FormGroup>
                          <label>Contract address</label>
                          <Input className="mb-2"
                            type="text" 
                            value={plan.contract||''}
                            onChange={(e) =>
                              setPlan({ ...plan, contract: e.target.value })
                            }
                          />
                        </FormGroup>
                      </Col>
                      <Col className="pr-md-1" md="12">
                        <Row>
                          <Col className="pr-md-1" md="6">
                            <FormGroup>
                              <label>max Buy</label>
                              <Input className="mb-2"
                                type="text" 
                                value={plan.maxBuy||''}
                                onChange={(e) =>
                                  setPlan({ ...plan, maxBuy: e.target.value })
                                }
                              />
                            </FormGroup>
                          </Col>
                          <Col className="pr-md-1" md="6">
                            <FormGroup>
                              <label>profit</label>
                              <Input className="mb-2"
                                type="text" 
                                value={plan.minimumBenefit||''}
                                onChange={(e) =>
                                  setPlan({ ...plan, minimumBenefit: e.target.value })
                                }
                              />
                            </FormGroup>
                          </Col>
                          {/* <Col className="pr-md-1" md="3">
                            <FormGroup>
                              <label>gasX</label>
                              <Input className="mb-2"
                                type="text" 
                                value={plan.gasX||''}
                                onChange={(e) =>
                                  setPlan({ ...plan, gasX: e.target.value })
                                }
                              />
                            </FormGroup>
                          </Col>
                          <Col className="pr-md-1" md="3">
                            <FormGroup>
                              <label>gasY</label>
                              <Input className="mb-2"
                                type="text" 
                                value={plan.gasY||''}
                                onChange={(e) =>
                                  setPlan({ ...plan, gasY: e.target.value })
                                }
                              />
                            </FormGroup>
                          </Col> */}
                        </Row>
                        <Row>
                          <Col className="pr-md-1" md="12">
                            {/* <label className="formmm">Fee = token gas + gasX + gasY * profit</label> &nbsp; */}
                            <Button color="btn1 float-right" size="sm" onClick={addPlan}>
                              Save
                            </Button>
                          </Col>
                        </Row>
                        <Row>
                          <Col md={12}>
                              B:<a href={`${explorerURL}/address/${plan.buy_pu}`} target="_blank">{plan.buy_pu?plan.buy_pu.substr(0,15) + `(${plan.buy_balance})`:'-----------'}</a>&nbsp;
                          </Col>
                          <Col md={12}>
                              S:<a href={`${explorerURL}/address/${plan.sell_pu}`} target="_blank">{plan.sell_pu?plan.sell_pu.substr(0,15)+ `(${plan.sell_balance})`:'----------'}</a>
                          </Col>
                          <Col className="pr-md-1" md="6">
                            <Input className="mb-2"
                              type="text" 
                              value={bs}
                              onChange={(e) =>
                                setBS(e.target.value||'')
                              }
                            />
                          </Col>
                          <Col className="pr-md-1" md="6">
                            <Button color="btn1 btn-success" size="sm" onClick={changeB}>
                              B
                            </Button>
                            <Button color="btn1 btn-danger" size="sm" onClick={changeS}>
                              S
                            </Button>
                          </Col>
                        </Row>
                      </Col>
                    </Row>
                  </Form>
                }
              </CardBody>
            </Col>
          </Row>
        </Card>
        {/* logs table */}
        <Card>
          <CardHeader>
            <CardTitle tag="h4">Logs</CardTitle>
            <Button className="btn1 btn-warning" onClick={() => deleteLogs({ deleteAll: true })}>
              Delete All
            </Button>
          </CardHeader>
          <CardBody className="max-height-600">
            <Table responsive>
              <thead className="text-primary">
                <tr>
                  <th className="text-center">#</th>
                  <th>Token</th>
                  <th>Transaction</th>
                  <th>Time</th>
                  <th>Status</th>
                  <th>Actions</th>
                </tr>
              </thead>
              <tbody>
                {
                  logData.map((item, key) => {
                    return (
                      <tr key={key}>
                        <td className="text-center">
                          <div className="photo">
                            <img
                              alt="..."
                              src={require("assets/img/bitcoin.png").default}
                            />
                          </div>
                        </td>
                        <td><a href={`${explorerURL}/token/${item.token}`} target="_blank">{item.symbol}</a></td>
                        <td>
                          <a className="text-white" href={`${explorerURL}/tx/${item.buyTx}`} target="_blank">buy</a> &nbsp;
                          <a className="text-white" href={`${explorerURL}/tx/${item.tokenTx}`} target="_blank">token</a> &nbsp;
                          <a className="text-white" href={`${explorerURL}/tx/${item.sellTx}`} target="_blank">sell</a> &nbsp;
                        </td>
                        <td>{item.created}</td>
                        <td>
                          <Badge color={item.status == 1 ? "success" : "danger"}>
                            {
                              item.status == 1 ? "Yes" : "No"
                            }
                          </Badge>  
                        </td>
                        <td>
                          {
                            item.error &&
                            <Button className="btn-link btn-icon" color="success" size="sm" onClick={() => showErrorModal(item)}>
                              <i className="tim-icons icon-notes" />
                            </Button>
                          }
                          <Button className="btn-link" color="danger" size="sm" onClick={() => deleteLogs(item)} title="Delete">
                            <i className="tim-icons icon-simple-remove" />
                          </Button>
                        </td>
                      </tr>
                    );
                  })
                }
              </tbody>
            </Table>
          </CardBody>
        </Card>
      </div>
      {/* error modal */}
      <Modal modalClassName="modal-black" isOpen={errorModalStatus}>
        <div className="modal-header">
          <h4>Error Information</h4>
          <button
            aria-label="Close"
            className="close"
            data-dismiss="modal"
            type="button"
            onClick={closeErrorModal}
          >
            <i className="tim-icons icon-simple-remove" />
          </button>
        </div>
        {
          errorData &&
          <div className="modal-body padBtt word-breakall">
            {
              errorData.error
            }
          </div>
        }
      </Modal>
      {/* warning modal */}
      <Modal modalClassName="modal-black" isOpen={delModalStatus}>
        <div className="modal-header">
          <h4>Will you delete all pairs?</h4>
          <button
            aria-label="Close"
            className="close"
            data-dismiss="modal"
            type="button"
            onClick={closeDelModal}
          >
            <i className="tim-icons icon-simple-remove" />
          </button>
        </div>
        <Col>
        </Col>
        <Button color="warning " className="btn1" onClick={() => delPairAll()}>Sure</Button>
      </Modal>
    </>
  );
};
const mapStateToProps = (state) => {
  const { LoginReducer } = state;
  return { credential: LoginReducer };
};
export default connect(mapStateToProps)(SAND1);
